import React, { useEffect, useState } from "react"
import {
  View,
  StyleSheet,
  Text,
  Alert,
  SafeAreaView,
  Image,
  TouchableOpacity
} from "react-native"
import Button from "../../components/Button"
import Header from "../../components/Header"
import { colors } from "../../utils/colors"
import { isWeb } from "../../utils/isweb"
import { useDispatch } from "react-redux"
import ImageUploader from "../../components/ImageUploader"

const AddLicense = ({ navigation }) => {
  const dispatch = useDispatch()
  const [image, setImage] = useState(null)

  useEffect(() => {
    navigation.getParent()?.setOptions({
      tabBarStyle: {
        display: "none"
      }
    })
  }, [navigation])

  const onUpload = () => {
    navigation.navigate("Credentials")
  }
  return (
    <SafeAreaView style={styles.body}>
      <View>
        <Header
          title="Driver License"
          onPress={() => navigation.goBack()}
          onboard={true}
          close={true}
        />
        <ImageUploader onUpload={setImage}>
          <Image
            style={styles.icon}
            resizeMode="contain"
            source={
              image ? { uri: image.uri } : require("../../assets/license.jpeg")
            }
          />
        </ImageUploader>
        <Text style={styles.title}>Front of Your ID</Text>
        <Text style={styles.titleContent}>
          Hold up your ID and take a picture. Your entire ID must be in the
          frame.
        </Text>
        <Button
          onPress={() => onUpload()}
          name="Capture"
          backgroundColor={colors.orange}
          color={colors.white}
        />
      </View>
    </SafeAreaView>
  )
}
const styles = StyleSheet.create({
  icon: {
    alignSelf: "center",
    width: 300,
    height: 200,
    borderRadius: 20,
    margin: "40%"
  },
  title: {
    fontSize: 24,
    color: colors.white,
    fontWeight: "bold",
    textAlign: "left",
    marginTop: 40,
    marginLeft: 20,
    marginBottom: 15
  },
  titleContent: {
    fontSize: 14,
    color: "#BCBCBC",
    textAlign: "left",
    paddingHorizontal: 20,
    marginBottom: 30
  },
  body: {
    padding: 15,
    flex: 1,
    backgroundColor: colors.black,
    alignItems: isWeb() ? "center" : null
  }
})
export default AddLicense
