import React, { useState } from "react"
import { View, StyleSheet, Text, Alert, SafeAreaView } from "react-native"
import { colors } from "../utils/colors"
import Header from "../components/Header"
import Input from "../components/Input"
import Button from "../components/Button"
import { useDispatch } from "react-redux"
import { resetPassword } from "../redux/auth/actions"
import { isWeb } from "../utils/isweb"

const SetNewPassword = ({ navigation }) => {
  const dispatch = useDispatch()
  const [values, setValues] = useState({})

  const onChange = (key, value) => {
    setValues({
      ...values,
      [key]: value
    })
  }

  const onNewPassword = () => {
    if (!values.new_password1 || !values.new_password2) {
      Alert.alert("Please enter password ")
    } else if (values.new_password1 !== values.new_password2) {
      Alert.alert("Password don`t match! ")
    } else {
      console.log("object", values)
      // dispatch(passConfirm(values, navigation))
      navigation.navigate("PasswordSuccess")
    }
  }
  return (
    <SafeAreaView style={styles.body}>
      <View>
        <Header
          title="New Password"
          onPress={() => navigation.goBack()}
          back={true}
          width={"80%"}
        />
        <View style={{ marginTop: 30 }} />
        <Input
          onChangeText={v => onChange("new_password1", v)}
          value={values.new_password1}
          title="New password"
          placeholder="Minimum 8 characters"
          password={true}
        />
        <Input
          onChangeText={v => onChange("new_password2", v)}
          value={values.new_password2}
          title="Confirm password"
          placeholder="Minimum 8 characters"
          password={true}
        />
        <View style={{ marginTop: "50%" }} />
        <Button
          onPress={() => onNewPassword()}
          name="Submit"
          backgroundColor={colors.orange}
          color={colors.white}
          borderRadius={30}
        />
      </View>
    </SafeAreaView>
  )
}
const styles = StyleSheet.create({
  title: {
    fontSize: 20,
    color: colors.blue,
    fontWeight: "bold",
    textAlign: "center",
    marginTop: 40,
    marginBottom: 15
  },
  titleContent: {
    fontSize: 16,
    color: colors.textColor,
    textAlign: "center",
    paddingHorizontal: 20,
    marginBottom: 30
  },
  body: {
    padding: 15,
    backgroundColor: colors.backgroundColor,
    alignItems: isWeb() ? "center" : null
  }
})
export default SetNewPassword
