import React, { useRef, useState } from "react"
import {
  View,
  StyleSheet,
  Text,
  Alert,
  SafeAreaView,
  TextInput
} from "react-native"
import { colors } from "../utils/colors"
import Header from "../components/Header"
import Input from "../components/Input"
import Button from "../components/Button"
import { useDispatch } from "react-redux"
import { resetPassword } from "../redux/auth/actions"
import { isWeb } from "../utils/isweb"
import CodeInputDisplay from "../components/CodeInputDisplay"

const TokenInput = ({ navigation }) => {
  const dispatch = useDispatch()
  const [values, setValues] = useState({})
  const [code, setCode] = useState("")
  const inputRef = useRef()

  const onChange = (key, value) => {
    setValues({
      ...values,
      [key]: value
    })
  }
  const onSubmit = () => {
    if (!code) {
      Alert.alert("Please enter token!")
    } else if (code.length !== 4) {
      Alert.alert("Please enter 4-digit token code!")
    } else {
      navigation.navigate("SetNewPassword", { item: values })
    }
  }
  return (
    <SafeAreaView style={styles.body}>
      <View>
        <Header
          title="Submit Token"
          onPress={() => navigation.goBack()}
          back={true}
          width={"80%"}
        />
        <View style={{ marginTop: "5%" }} />
        <TextInput
          textStyle={styles.inputText}
          style={styles.input}
          value={code}
          onChangeText={newValue => setCode(newValue)}
          keyboardType="phone-pad"
          autoFocus
          maxLength={4}
          ref={inputRef}
        />
        <Text style={{ marginLeft: 15, marginBottom: 10 }}>
          Enter the 4-digit code
        </Text>
        <CodeInputDisplay
          code={code}
          onPress={() => inputRef?.current.focus()}
        />
        <View style={{ marginTop: "50%" }} />
        <Button
          onPress={() => onSubmit()}
          name="Submit"
          backgroundColor={colors.orange}
          color={colors.white}
          borderRadius={30}
        />
      </View>
    </SafeAreaView>
  )
}
const styles = StyleSheet.create({
  title: {
    fontSize: 20,
    color: colors.blue,
    fontWeight: "bold",
    textAlign: "center",
    marginTop: 40,
    marginBottom: 15
  },
  titleContent: {
    fontSize: 16,
    color: colors.textColor,
    textAlign: "center",
    paddingHorizontal: 20,
    marginBottom: 30
  },
  body: {
    padding: 15,
    backgroundColor: colors.backgroundColor,
    alignItems: isWeb() ? "center" : null
  },
  inputText: {
    height: 40,
    color: "#fff"
  },
  input: {
    height: 40,
    opacity: 0,
    // position: "absolute",
    zIndex: 100,
    width: "100%"
  }
})
export default TokenInput
